  position: relative;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
  transition: transform 200ms ease;
  pointer-events: none;
}

.burger__item::before {
  content: "";
  position: absolute;
  top: 10px;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
}

.burger__item::after {
  content: "";
  position: absolute;
  top: -10px;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
}

.burger.close .burger__item {
  transform: rotate(-45deg);
}

.burger.close .burger__item:after {
  transform: rotate(90deg);
  top: 0;
}

.burger.close .burger__item::before {
  display: none;
}

