.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1eb43e;
  visibility: visible;
  opacity: 1;
}

.preloader.preloader_hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s, visibility 0s 1s;
}

.spinner {
  width: 50px;
  height: 50px;
  position: relative;
  margin: 100px auto;
}

.double-bounce1,
.double-bounce2,
.double-bounce3 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #dadada;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.double-bounce3 {
  background-color: #757575;
}

.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.double-bounce3 {
  -webkit-animation-delay: -2s;
  animation-delay: -2s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

.popup {
  background-color: #fff;
  width: 410px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
  border-radius: 25px;
  opacity: 0;
  visibility: hidden;
}

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(2, 2, 2, 0.959);
  opacity: 0;
  visibility: hidden;
}

.popup__close {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 20;
}

.popup__content {
  padding: 25px;
}

.popup__title {
  font-size: 1.7rem;
  margin-top: -6px;
  margin-bottom: 50px;
}

.popup.active {
  opacity: 1;
  visibility: visible;
  overflow: scroll;
}

.popup.active + .popup__overlay {
  opacity: 1;
  visibility: visible;
}

.form__item {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 15px;
}

.form__item .valid + label {
  top: 0;
  color: #1eb43e;
  z-index: 3;
}

.form__item .valid + label::before {
  width: 95%;
  height: 20px;
  content: "";
  background-color: white;
  position: absolute;
  top: 0px;
  left: 10%;
  z-index: -1;
}

.form__item input {
  width: 100%;
  box-sizing: border-box;
  padding: 16px;
  border-radius: 10px;
  border: 1px solid #a7a7a7;
  transition: border 200ms ease;
  outline: none;
  cursor: pointer;
}

.form__item input:hover {
  border: 1px solid #1eb43e;
}

.form__item input:focus + label {
  color: #1eb43e;
  top: 0;
  z-index: 3;
}

.form__item input:focus + label::before {
  width: 95%;
  height: 20px;
  content: "";
  background-color: white;
  position: absolute;
  top: 0px;
  left: 10%;
  z-index: -1;
}

.form__item label {
  color: #242424;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 16px;
  transition: all 200ms ease-in-out;
}

.form__item select {
  padding: 14px 16px 14px 10px;
  border-radius: 10px;
  border: 1px solid #a7a7a7;
  transition: border 200ms ease;
  outline: none;
  cursor: pointer;
  height: 50px;
  -webkit-appearance: none;
}

.form__item select:hover {
  border: 1px solid #1eb43e;
}

